<template>
  <g-body-layout
    :pageIndex="pageNumber"
    :pageSize="pageSize"
    :total="total"
    :filterText.sync="filterText"
    @pageIndexChange="pageIndexChange"
    @pageSizeChange="pageSizeChange"
    @suffixSearchCallback="onFilterReady"
  >
    <!-- 按钮组插槽 -->
    <template #btnGroup>
      <g-btn-group :buttonLists="btnList" @btnCallBack="btnGroupClick">
      </g-btn-group>
    </template>
    <!-- g-ndo-table插槽 -->
    <template #table>
      <g-ndo-table
        :id="id"
        :handName="'培训需求'"
        :dataList="dataList"
        :isTableLoading="loading"
        :total="total"
        @rowClick="rowClick"
      ></g-ndo-table>
    </template>
    <!-- 详细内容插槽 -->
    <template #content>
      <create-or-edit-training-requirement
        :ndoEditParams="ndoEditParams"
        :refreshTag="refreshTag"
        :saveTag="saveTag"
        :resetTag="resetTag"
        @childRefresh="childRefresh($event)"
        @rowClick="rowClick($event)"
        v-if="isShowEdit"
      ></create-or-edit-training-requirement>
      <g-empty v-else> </g-empty>
    </template>
  </g-body-layout>
</template>

<script>
import CreateOrEditTrainingRequirement from "./create-or-edit-training-requirement/create-or-edit-training-requirement.vue";
import { GNdoPageBase } from "@/utils";

export default {
  name: "TrainingRequirement",
  mixins: [GNdoPageBase],
  components: {
    CreateOrEditTrainingRequirement,
  },

  data() {
    return {
      isTableLoading: false,
      filterText: "",
    };
  },

  created() {
    this.getBtnList();
  },

  methods: {
    getBtnList() {
      // 获取按钮列表
      this.btnList = [
        {
          text: this.AppConst.button.create,
          icon: this.AppConst.icon.create,
          eventName: this.AppConst.button.create,
          display: true,
        },
        {
          text: this.AppConst.button.copy,
          icon: this.AppConst.icon.copy,
          eventName: this.AppConst.button.copy,
          display: !!this.id,
        },
        {
          text: this.AppConst.button.delete,
          icon: this.AppConst.icon.delete,
          eventName: this.AppConst.button.delete,
          display: !!this.id,
        },
        {
          text: this.AppConst.button.refresh,
          icon: this.AppConst.icon.refresh,
          eventName: this.AppConst.button.refresh,
          right: true,
          display: this.isShowEdit,
        },
        {
          text: this.AppConst.button.save,
          icon: this.AppConst.icon.save,
          eventName: this.AppConst.button.save,
          right: true,
          type: "primary",
          display: this.isShowEdit,
        },
      ];
    },

    add() {
      this.handleAdd();
    },

    edit() {
      if (!this.id) {
        this.$message.warning("请先选择一条记录");
        return;
      }
      this.handleEdit();
    },

    delete() {
      if (!this.id) {
        this.$message.warning("请先选择一条记录");
        return;
      }
      this.$confirm({
        title: "确认删除",
        content: "是否确认删除该培训需求？",
        okText: "确认",
        cancelText: "取消",
        onOk: () => {
          // TODO: 调用删除API
          this.$message.success("删除成功");
          this.refreshData();
        },
      });
    },

    fetchData(request) {
      this.loading = true;
      setTimeout(() => {
        request.finishedCallback();
        // 模拟数据
        let dataList = [];
        for (let i = 0; i < 25; i++) {
          dataList[i] = {
            id: i + 1,
            name: "培训需求" + (i * this.pageNumber + 1),
            department: "部门" + (i % 5 + 1),
            position: "岗位" + (i % 3 + 1),
            priority: ["高", "中", "低"][i % 3],
            status: ["待审核", "已审核", "已完成"][i % 3],
            createTime: "2024-" + ((i % 12) + 1) + "-" + ((i % 28) + 1),
          };
        }

        if (this.filterText) {
          dataList = dataList.filter((item) => {
            return item.name.indexOf(this.filterText) > -1;
          });
        }

        request.successCallback({
          items: dataList,
          totalCount: dataList.length * 2,
        });
      }, 1000);
    },
  },
};
</script>

<style lang="scss" scoped></style>
