<template>
  <a-spin :spinning="loading">
    <a-form-model
      ref="form"
      :model="entity"
      :rules="rules"
      autocomplete="off"
      layout="vertical"
    >
      <div class="card">
        <a-row :gutter="24">
          <a-col :sm="6">
            <a-form-model-item label="需求名称" prop="requirementName">
              <a-input
                v-model="entity.requirementName"
                placeholder="请输入需求名称"
                @change="inChange"
              />
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <a-form-model-item label="所属部门" prop="department">
              <a-select
                v-model="entity.department"
                placeholder="请选择所属部门"
              >
                <a-select-option value="研发部">研发部</a-select-option>
                <a-select-option value="生产部">生产部</a-select-option>
                <a-select-option value="质量部">质量部</a-select-option>
                <a-select-option value="人力资源部">人力资源部</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <a-form-model-item label="岗位" prop="position">
              <a-select
                v-model="entity.position"
                placeholder="请选择岗位"
              >
                <a-select-option value="工程师">工程师</a-select-option>
                <a-select-option value="技术员">技术员</a-select-option>
                <a-select-option value="操作员">操作员</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <a-form-model-item label="优先级" prop="priority">
              <a-select
                v-model="entity.priority"
                placeholder="请选择优先级"
              >
                <a-select-option value="高">高</a-select-option>
                <a-select-option value="中">中</a-select-option>
                <a-select-option value="低">低</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="24">
          <a-col :sm="12">
            <a-form-model-item label="培训内容" prop="content">
              <a-textarea
                v-model="entity.content"
                placeholder="请输入培训内容"
                :rows="4"
                :max-length="1000"
              />
            </a-form-model-item>
          </a-col>
          <a-col :sm="12">
            <a-form-model-item label="预期目标" prop="target">
              <a-textarea
                v-model="entity.target"
                placeholder="请输入预期目标"
                :rows="4"
                :max-length="1000"
              />
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="24">
          <a-col :sm="6">
            <a-form-model-item label="计划开始时间" prop="startTime">
              <a-date-picker
                v-model="entity.startTime"
                style="width: 100%"
                placeholder="请选择开始时间"
              />
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <a-form-model-item label="计划结束时间" prop="endTime">
              <a-date-picker
                v-model="entity.endTime"
                style="width: 100%"
                placeholder="请选择结束时间"
              />
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <a-form-model-item label="预计培训人数" prop="expectedTrainees">
              <a-input-number
                v-model="entity.expectedTrainees"
                :min="1"
                style="width: 100%"
                placeholder="请输入预计培训人数"
              />
            </a-form-model-item>
          </a-col>
        </a-row>
      </div>

      <g-collapse headerName="其他信息">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="6">
              <a-form-model-item label="创建人">
                <span>{{ entity.creator || 'admin' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <a-form-model-item label="创建时间">
                <span>{{ entity.createTime || '2024-01-01 12:00:00' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <a-form-model-item label="状态">
                <span>{{ entity.status || '待审核' }}</span>
              </a-form-model-item>
            </a-col>
          </a-row>
        </template>
      </g-collapse>
    </a-form-model>
  </a-spin>
</template>

<script>
import { GNdoPageEditBase } from "@/utils";

export default {
  name: "CreateOrEditTrainingRequirement",
  mixins: [GNdoPageEditBase],

  data() {
    return {
      entity: {
        requirementName: "",
        department: undefined,
        position: undefined,
        priority: undefined,
        content: "",
        target: "",
        startTime: null,
        endTime: null,
        expectedTrainees: 1,
        creator: "",
        createTime: "",
        status: "",
      },
      rules: {
        requirementName: [
          { required: true, message: "需求名称不能为空", trigger: "change" },
          {
            pattern: /^[^\s]+(\s+[^\s]+)*$/,
            message: "名称不能包含空格",
            trigger: "change",
          },
        ],
        department: [
          { required: true, message: "请选择所属部门", trigger: "change" },
        ],
        position: [
          { required: true, message: "请选择岗位", trigger: "change" },
        ],
        priority: [
          { required: true, message: "请选择优先级", trigger: "change" },
        ],
        content: [
          { required: true, message: "培训内容不能为空", trigger: "blur" },
        ],
        target: [
          { required: true, message: "预期目标不能为空", trigger: "blur" },
        ],
        startTime: [
          { required: true, message: "请选择开始时间", trigger: "change" },
        ],
        endTime: [
          { required: true, message: "请选择结束时间", trigger: "change" },
        ],
        expectedTrainees: [
          { required: true, message: "请输入预计培训人数", trigger: "change" },
        ],
      },
    };
  },

  methods: {
    init() {
      this.loading = true;
      if (this.id) {
        // 模拟请求
        setTimeout(() => {
          this.loading = false;
          this.entity = {
            requirementName: "测试培训需求",
            department: "研发部",
            position: "工程师",
            priority: "高",
            content: "培训内容描述",
            target: "培训目标描述",
            startTime: "2024-02-01",
            endTime: "2024-02-28",
            expectedTrainees: 10,
            creator: "admin",
            createTime: "2024-01-01 12:00:00",
            status: "待审核",
          };
        }, 500);
      } else {
        this.reset();
        this.loading = false;
      }
    },

    resetFormData() {
      this.entity = {
        requirementName: "",
        department: undefined,
        position: undefined,
        priority: undefined,
        content: "",
        target: "",
        startTime: null,
        endTime: null,
        expectedTrainees: 1,
        creator: "",
        createTime: "",
        status: "",
      };
    },

    async save() {
      let res = await this.checkError();
      if (res) {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.saveSuccess(this.entity.requirementName);
          this.handleSaveEndBackView(this.id);
          this.refreshParent({
            reset: null,
          });
        }, 500);
      }
    },

    inChange(e) {
      console.log(e.target.value);
    },
  },
};
</script>

<style lang="scss" scoped>
.card {
  background: #fff;
  padding: 24px;
  margin-bottom: 24px;
}
</style>